import React from "react";
import { Panel } from '@vkontakte/vkui';
import { PanelHeader } from '@vkontakte/vkui';
import { PanelHeaderButton } from '@vkontakte/vkui';
import { PanelHeaderBack } from '@vkontakte/vkui';
import { Group } from '@vkontakte/vkui';
import { Input } from '@vkontakte/vkui';
import { FormLayout } from '@vkontakte/vkui';
import { Button } from '@vkontakte/vkui';
import { Div } from '@vkontakte/vkui';
import { Select } from '@vkontakte/vkui';
import { Spinner } from '@vkontakte/vkui';
import { FormLayoutGroup } from '@vkontakte/vkui';

import { Utils } from "../Utils"
import Icon24Flash from '@vkontakte/icons/dist/24/flash';

import $ from "jquery";


class DeadFollowers extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            btnEnabled: true,
            selector: true,
        }
        //$("#DeadFollowersInput").parents(".FormLayout__row").hide();
    }
    DeadFollowersGet = () => {
        if ($("#DeadFollowersButton").is(':disabled')) return;


        $("#DeadFollowersInput").prop("disabled", true);
        $("#DeadFollowersButton").prop("disabled", true);
        this.setState({ btnEnabled: false });


        $("#DeadFollowersResult").slideDown("fast", () => {
            $("#DeadFollowersUserId").prop("disabled", false);
            $("#DeadFollowersButton").prop("disabled", false);
            this.setState({ btnEnabled: true });
        });

        return;
    }
    render() {
        return (
            <Panel id={this.props.id}>
                <PanelHeader
                    addon={<PanelHeaderButton onClick={this.props.goBack}>Назад</PanelHeaderButton>}
                    left={<PanelHeaderBack onClick={this.props.goBack} />}
                >Мертвые подписчики</PanelHeader>
                <Group>
                    <FormLayout>
                        <Select placeholder="Выберите">
                            <option value="m">Мужской</option>
                            <option value="f">Женский</option>
                        </Select>

                        {!this.state.selector &&
                            <Input top="Ссылка на пользователя/группу" id="DeadFollowersInput" />
                        }

                        <Button id="DeadFollowersButton" size="xl" before={this.state.btnEnabled ? <Icon24Flash /> : ""} onClick={this.DeadFollowersGet}>{this.state.btnEnabled ? "Узнать" : <Spinner size="regular" />}</Button>
                    </FormLayout>
                </Group>
                < Div id="DeadFollowersResult">
                    {this.state.result}
                </Div>
            </Panel>

        )
    }
}
export default DeadFollowers;